import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: 'home', loadChildren: './home/home.module#HomePageModule' },
  { path: 'page-upload', loadChildren: './page-upload/page-upload.module#PageUploadPageModule' },
  { path: 'modal-camera', loadChildren: './modal-camera/modal-camera.module#ModalCameraPageModule' },
  { path: 'modal-media', loadChildren: './modal-media/modal-media.module#ModalMediaPageModule' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
