import { Component, OnInit } from '@angular/core';
import { NavController,AlertController,ModalController } from '@ionic/angular';
import { ImageCroppedEvent } from 'ngx-image-cropper';

import { ApiService } from '../services/api/api.service';

@Component({
  selector: 'app-modal-media',
  templateUrl: './modal-media.page.html',
  styleUrls: ['./modal-media.page.scss'],
})
export class ModalMediaPage implements OnInit {

  constructor(private modalCtrl:ModalController, public apiService : ApiService) { }

  imageChangedEvent: any = '';
  croppedImage: any = '';
  photoType = '';
  isSubmit = false;

  ngOnInit() {
    // console.log(this.photoType);
  }

  fileChangeEvent(event: any): void {
    this.imageChangedEvent = event;
  }

  imageCropped(event: ImageCroppedEvent) {
    this.croppedImage = event.base64;
  }
  imageLoaded() {
      // show cropper
  }
  loadImageFailed() {
      // show message
  }

  submitCrop() {
    this.isSubmit = true;
    var originPhoto = this.croppedImage;
    var cropImage = this.croppedImage.replace("data:image/png;base64,","");
    if (this.photoType == 'DriverLicence') {
      this.apiService.scanDriverLicences(cropImage).then(data => {
        // console.log(data);
        this.modalCtrl.dismiss({
          'photo': originPhoto,
          'response' : data
        });
        this.isSubmit = false;
      });
    }
    else if (this.photoType == 'CarDetail') {
      this.apiService.scanCar(cropImage).then(data => {
        // console.log(data);
        this.modalCtrl.dismiss({
          'photo': originPhoto,
          'response': data
        });
        this.isSubmit = false;
      });
    }
    else if (this.photoType == 'Rego') {
      this.apiService.scanRego(cropImage).then(data => {
        // console.log(data);
        this.modalCtrl.dismiss({
          'photo': originPhoto,
          'response': data
        });
        this.isSubmit = false;
      });
    }
    else if (this.photoType == 'VIN') {
      this.apiService.scanVin(cropImage).then(data => {
        // console.log(data);
        this.modalCtrl.dismiss({
          'photo': originPhoto,
          'response': data
        });
        this.isSubmit = false;
      });
    }
    
  }

  closeModalMedia() {
    this.modalCtrl.dismiss(
      {
        'photo': null
      }
    );
  }

}
